.origin-source.with-link:after {
  display: block;
  position: absolute;
  border-color: transparent transparent transparent #f0f0f0;
  border-width: 7px;
  border-style: solid;
  height: 0;
  width: 0;
  top: 18px;
  right: 4px;
  line-height: 0;
  content: "";
}
